<template>
  <div class='markdown-container'>
    <MavonEditorModule :content='articleDetail'/>
    
    <div class='icon-area'>
      <BaseBackTop/>
      <BaseReturnButton/>
      <BaseNextButton/>
      <BasePreviousButton/>
    </div>
    
  </div>
</template>

<script>
import ArticleData from './data/ArticleData'
import MavonEditorModule from './childViews/MavonEditorModule'

export default {
  name:'ArticleDetail',
  data(){
    return {
      articleDetail:"",
      showPrevious:false,
      showNext:false
    }
  },
  mounted(){
    let type=this.$route.params.type;
    let index=this.$route.params.id;

    ArticleData.forEach(ele=>{
      if(ele.type==type){
        //判断按钮的显示和隐藏
        if(ele.articleNames.length==0){
          this.showPrevious=false;
          this.showNext=false;
        }else{
          if(index==0)  this.showPrevious=false;    //如果是第一篇文章，不显示上一篇按钮
          if(index==ele.articleNames.length-1) this.showNext=false;   //如果是最后一篇，不显示最后一篇按钮
        }

        this.articleDetail=ele.articleContent(ele.articleNames[index]);
      }
    });
    
  },
  components:{
    MavonEditorModule
  }
}
</script>

<style>
  .markdown-container{
    padding-top: 1.2rem;
    padding-bottom: 1.2rem;
  }

  .icon-area{
    position: fixed;
    right: 0.3rem;
    bottom: 1.2rem;
    text-align: center;
    font-size: 0.18rem;
    color: red;
    z-index: 1501;
  }
</style>